@import url(https://fonts.googleapis.com/css2?family=Roboto:100);
.calc-body {
  width: 300px;
  height: 375px;
  box-shadow: 0 0 25px 0 #aaa;
}

.calc-screen {
  text-align: right;
  color: white;
  background-color: #858694;
  line-height: 100px;
  font-size: 3em;
  padding: 0 10px;
  right: 0;
  -webkit-transform-origin: right;
          transform-origin: right;
}

.calc-buttons {
  width: 74px;
  height: 55px;
  flex-grow: 1;
  align-items: center;
  margin: 0 auto;
  line-height: 55px;
}

button {
  display: block;
  background: none;
  border: none;
  padding: 0;
  font-family: inherit;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: pointer;
  outline: none;
  border-top: 1px solid #777;
  border-right: 1px solid #777;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

button:active {
  box-shadow: inset 0 0 80px 0 rgba(0, 0, 0, 0.25);
}

.calc-buttons button {
  border-top: 1px solid #777;
  border-right: 1px solid #777;
}

.keyboard {
  display: flex;
  border: 1px solid #858694;
}

.key-digits {
  background: #e0e0e7;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.key-digits button {
  background: #e0e0e7;
}

.operators button {
  width: 75px;
  background: linear-gradient(to bottom, rgba(252, 156, 23, 1) 0%, rgba(247, 126, 27, 1) 100%);
}

.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  min-height: 100vh;
  font: 100 20px 'Roboto', sans-serif;
}

